<script>
export default {
  data () {
    return {}
  },
  render () {
    const { $route: { meta: { link } } } = this
    if ({ link }.link === '') {
      return '404'
    }
    const url = { link }.link
    const height = document.documentElement.clientHeight - 94.5 + 'px'
    const style = { height: height }

    return (
      <div style={style}>
        <iframe
          src={url}
          frameborder="no"
          style="width: 100%; height: 100%"
          scrolling="auto"
        ></iframe>
      </div>
    )
  }
}
</script>
